@import url(//db.onlinewebfonts.com/c/9ae45999d1bf30fd41eec7b4fc077c08?family=ITC+Benguiat+Gothic);

/* Background */
.body-direc{
    background-color: #ffffff;
    text-align: center;
    font-family: "ITC Benguiat Gothic", sans-serif;
}
/*contenedores*/
.cont-header-direc{
    width: 100%;
    margin-top: 5%;
    text-align: left;
    margin-left: 2%;
    margin-bottom: 2%;
}
.cont-headerBotones-direc{
    float: right;
    margin-right: 10%;
    margin-top: 2.5%;
    width: 15%;
}
.cont-main-direc{
    padding: 5%;
    background-color: #cbcbcb;
    margin: 5% 12% 5% 12%;
    border-radius: 2px;
    border: none;
    box-shadow: #3d3d3d;
    display: flex;
    text-align: center;
}
.cont-opcion-direc{
    margin-right: 2%;
    margin-left: 2%;
    width: 45%;
    padding: 3% 5%;
    background: #ecebeb;
    border-radius: 10px;
    text-align: center;
}
.cont-opcion-direc:hover{
    transition: 1s;
    background: linear-gradient(90deg, #1d70b7 50%, #3ba1ff 100%);
    color: white;
}
.cont-footer-direc{
    background-color: #02498f;
    padding: 4%;
    color: rgba(255, 255, 255, 0.53);
}

/*imgs*/
.float-left{
    float: left;
    width: 9.5%;
    margin-right: 4%;
}
.img-directorio{
    width: 70%;
}
.img-opcion-direc{
    width: 30%;
}
/*Texto*/
.directorio-title{
    text-align: left;
    margin: 5% 0 5% 15%;
    padding-top: 2%;
}

/* Botones */
.boton-cerrarSesion{
    border: none;
    border-radius: 10px;
    padding: 5% 16%;
    height: 40px;
    font-family: "ITC Benguiat Gothic", sans-serif;
    font-weight: bold;
    background-color: #ececec;
}
.boton-cerrarSesion:hover{
    transition: 800ms;
    cursor: pointer;
    background: linear-gradient(90deg, #1d70b7 50%, #1b8cee 100%);
    color: white;
}
.boton-opcion-direc{
    border: none;
    border-radius: 40px;
    background: none;
    width: 100%;
    transition: 1s;
    padding: 5%;
}
.boton-opcion-direc:hover{
    transition : 1s;
    padding: 10%;
    cursor: pointer;
    color: white;
}

/*Lista*/
.ul{
    list-style: none;
    padding: 0;
    margin: 0;
}
.li-flex{
    margin-bottom: 2%;
    display: flex;
}
