@import url(//db.onlinewebfonts.com/c/9ae45999d1bf30fd41eec7b4fc077c08?family=ITC+Benguiat+Gothic);
.body-cursos{
    background-color: #ffffff;
    text-align: center;
    font-family: "ITC Benguiat Gothic", sans-serif;
}
/*Contenedores*/
.cont-header-cursos{
    width: 50%;
    margin-top: 5%;
    text-align: left;
    margin-left: 2%;
    margin-bottom: 2%;
}
.cont-main-cursos{
    padding: 5%;
    background: linear-gradient(90deg, #1d70b7 50%, #002d50 100%);
    margin: 3% 2%;
    border-radius: 20px;
    border: none;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    text-align: left;
}
.cont-btn-regresar-cursos{
    display: flex;
    margin-top: 0;
    margin-bottom: 3%;
    width: 100%;
    background: none;
    text-align: right;
}
.cont-curso-existente{
    border-collapse: separate;
    border-spacing: 0px 10px;

    margin: 5% 0 10% 0;
    border-radius: 10px;
    width: 100%;
    background-color: #ececec;
    padding: 2%;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    transition: 800ms;
}
.cont-btn-modificar-curso{
    width: 30%;
    text-align: center;
}

/*Botones*/
.btn-regresar-cursos{
    background: none;
    border: none;
    border-radius: 10px;
    padding: 1% 1.5%;
    font-family: "ITC Benguiat Gothic", sans-serif;
    transition: 800ms;
    color: white;

}
.btn-regresar-cursos:hover{
    color: #679ff1;
    cursor: pointer;
}

.btn-modificar-curso{
    margin-top: 5%;
    background: #1d70b7;
    border: none;
    border-radius: 10px;
    padding: 3.5% 15%;
    font-family: "ITC Benguiat Gothic", sans-serif;
    transition: 800ms;
    color: white;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
}
.btn-modificar-curso:hover{
    background: linear-gradient(90deg, rgb(248, 177, 51) 2%, rgb(255, 255, 255) 100%);
    cursor: pointer;
    color: #1d70b7;
    font-weight: bold;
}

/*lista*/
.ul-cursos{
    width: 90%;
    list-style: none;
}

/*Imágenes*/
.fl-left-lg{
    float: left;
    width: 50%;
    margin-right: 2%;
    margin-left: 2%;
}
/*Texto*/
.interlineado-s{
    line-height: 5%;
}
.texto-estatus-curso{
    color: #f8b133;
}

/*Tablas*/
.table-curso-data{
    text-align: center;
    width: 50%;
   
}

.select-estatus{
    border-radius: 5px;
    border: none;
    width: 60%;
    height: 20px;
    padding: 5%;
    font-family: "ITC Benguiat Gothic", sans-serif;
    background-color: #ececec;
    margin-bottom: 3%;
    margin-right: 5%;
    text-align: right;
    transition: 800ms;
}
.select-estatus:hover{
    background-color: rgba(119, 157, 190, 0.31);
    color: white;
}

.select-alIns{
    border-radius: 5px;
    border: none;
    width: 60%;
    padding: 5% 10%;
    font-family: "ITC Benguiat Gothic", sans-serif;
    background-color: rgba(236, 236, 236, 0);
    margin-bottom: 3%;
    margin-right: 5%;
    text-align: center;
    transition: 800ms;
    font-size: 100%;
}
.select-alIns:hover{
    background-color: rgba(119, 157, 190, 0.31);
    color: #f8b133;
}