@import url(//db.onlinewebfonts.com/c/9ae45999d1bf30fd41eec7b4fc077c08?family=ITC+Benguiat+Gothic);

/* Background */
.body-acceso{
    background-color: #f5f5f5;
    text-align: center;
}
/*Justificaciones*/
.margin-top{
    margin-top: 40px;
    margin-bottom: 20px;
}
/*Contenedores*/

.cont-imgNivel{
    margin-top: 6%;
    padding: 5%;
    width: 50%;
}
.cont-inicioSesion{
    margin-left: 50px;
    float: right;
    width: 50%;
    text-align: center;
    margin-top: 5%;
    margin-right: 20px;
    background: linear-gradient(90deg, #1d70b7 50%, #0c3f6b 100%);
    color: #0066cc;
    padding: 10px;
    border-radius: 10px;
    font-family: "ITC Benguiat Gothic", sans-serif;
}
.cont-acceso{
    margin-top: 20px;
    width: 100%;
}
.cont-li-izquierda{
    text-align: left;
    margin-left: 40px;
}
.cont-footer{
    background-color: #d3d3d3;
    padding-bottom: 2%;
    padding-top: 2%;
    margin-bottom: 0;
}

/*Titulos*/
.h1-nivel-blanco{
    font-family: "ITC Benguiat Gothic", sans-serif;
    font-weight: 1000;
    color: #ffffff;
}
/*Footer*/
.nivel-azul{
    font-family: "ITC Benguiat Gothic", sans-serif;
    color: #1d70b7;
}

/*Labels*/
.label-input-acceso{
    font-family: "ITC Benguiat Gothic", sans-serif;
    color: white;
    font-weight: bold;
}

/*Imgs*/
.logo{
    width: 60%;
    height: 60%;
}

/*Inputs*/
.input-acceso{
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: rgba(255, 255, 255, 0.65);
    border-radius: 50px;
    border: none;
    width: 70%;
    height: 40px;
    color: white;
    padding-left: 5%;
    font-family: "ITC Benguiat Gothic", sans-serif;

}

/*Botones*/
.btn-acceso{
    margin-top: 10px;
    margin-bottom: 40px;
    border-radius: 10px;
    border: none;
    height: 40px;
    width: 25%;
    font-family: "ITC Benguiat Gothic", sans-serif;
    font-weight: bold;
    font-size: 18px;
    padding: 10px;
    cursor: pointer;
    background: white;
}
.btn-acceso:hover{
    transition : 700ms;
    width: 30%;
    background: linear-gradient(90deg, #ffffff 0%, #f8b133 100%);
    color: #e5332a;

}