@import url(//db.onlinewebfonts.com/c/9ae45999d1bf30fd41eec7b4fc077c08?family=ITC+Benguiat+Gothic);
.body-buscar{
    background-color: #1d70b7;
    text-align: center;
    font-family: "ITC Benguiat Gothic", sans-serif;
    display: flex;
}
.cont-deco{
    margin: 2% 3% 0 1%;
    padding: 10% 5%;
    background: linear-gradient(90deg, #09253d 50%, #7dbcf3 100%);
    position: absolute;
    text-align: left;
}
.cont-main-buscar{
    padding: 5%;
    margin: 5% 20% 0 30%;
    background: linear-gradient(90deg, rgba(255, 255, 255, 0.69) 50%, rgba(255, 255, 255, 0.53) 100%);
    border-radius: 3px;
    position: absolute;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    display: flex;

}
.logo-buscar{
    width: 25%;
}
.ul-buscar{
    list-style: none;
}
.input-buscar{
    padding: 5% 30px;
    border-radius: 5px;
    border: none;
    font-family: "ITC Benguiat Gothic", sans-serif;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);

}

.btn-buscar{
    margin-top: 15%;
    border-radius: 10px;
    border: none;
    font-size: 18px;
    padding: 8% 30%;
    cursor: pointer;
    background: white;
    transition : 700ms;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    font-family: "ITC Benguiat Gothic", sans-serif;
}
.btn-buscar:hover{
    transition : 700ms;
    background: linear-gradient(90deg, #ffffff 0%, #f8b133 100%);
    color: #e5332a;
}

.btn-cancelar-buscar{
    margin: 30% 0 0 30%;
    border-radius: 10px;
    border: none;
    font-size: 18px;
    padding: 2% 10%;
    cursor: pointer;
    background: white;
    transition : 700ms;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
    font-family: "ITC Benguiat Gothic", sans-serif;
}
.btn-cancelar-buscar:hover{
    transition : 700ms;
    background: linear-gradient(90deg, #000f3a 0%, #0c43e1 100%);
    color: rgba(255, 255, 255, 0.87);
}