@import url(//db.onlinewebfonts.com/c/9ae45999d1bf30fd41eec7b4fc077c08?family=ITC+Benguiat+Gothic);

.body-cliente{
    text-align: center;
    font-family: "ITC Benguiat Gothic", sans-serif;
}
.cont-header-cliente{
    width: 50%;
    margin-top: 5%;
    text-align: left;
    margin-left: 2%;
    margin-bottom: 2%;
}
.cont-main-cliente{
    background: linear-gradient(90deg, #e3e3e3 50%, #05131e 100%);
    margin: 3% 10% 5% 10%;
    border: none;
    box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.2);
    text-align: left;
}
.cont-footer-direc{
    background-color: #02498f;
    padding: 4%;
    color: rgba(255, 255, 255, 0.53);
}

/*Imgs*/
.float-left{
    float: left;
    width: 9.5%;
    margin-right: 4%;
}

/*list*/
.ul{
    list-style: none;
}

/*Inputs*/
.input-number{
    width: 40%;
    border-radius: 5px;
    border: none;
    padding: 1% 10%;
    font-family: "ITC Benguiat Gothic", sans-serif;
    background-color: rgba(73, 73, 73, 0.17);
    margin-top: 3%;
    text-align: center;
}
.input-date{
    width: 40%;
    border-radius: 5px;
    border: none;
    padding: 1% 10%;
    font-family: "ITC Benguiat Gothic", sans-serif;
    background-color: rgba(73, 73, 73, 0.17);
    margin-top: 3%;
    text-align: right;
}
.input-text{
    border-radius: 5px;
    border: none;
    width: 70%;
    padding: 2% 10%;
    font-family: "ITC Benguiat Gothic", sans-serif;
    background-color: rgba(73, 73, 73, 0.17);
    margin-bottom: 3%;
    margin-right: 5%;
    text-align: right;
    box-shadow: 1px 1px 1px 1px rgba(89, 155, 255, 0.2);
}
.input-number1{
    width: 50%;
    border-radius: 2px;
    border: none;
    padding: 2% 10%;
    font-family: "ITC Benguiat Gothic", sans-serif;
    background-color: rgba(73, 73, 73, 0.17);
    margin-top: 3%;
    text-align: center;
}
.input-date1{
    width: 50%;
    border-radius: 2px;
    border: none;
    padding: 2% 10%;
    font-family: "ITC Benguiat Gothic", sans-serif;
    background-color: rgba(73, 73, 73, 0.17);
    margin-top: 3%;
    text-align: right;
}
.select-estado{
    width: 100%;
    border-radius: 2px;
    border: none;
    padding: 2% 10%;
    font-family: "ITC Benguiat Gothic", sans-serif;
    background-color: rgba(73, 73, 73, 0.17);
    margin-top: 3%;
    text-align: right;
}

/*labels*/
.label-date{
    font-size: small;
    color: rgba(73,73,73,0.53);
    margin-right: 5%;
    margin-bottom: 2%;
}

/*Botones*/
.btn-seleccionar-curso{
    padding: 5% 10%;
    border-radius: 10px;
    border: none;
    margin-right: 10%;
    font-family: "ITC Benguiat Gothic", sans-serif;
    transition: 800ms;
    background-color: #cbcbcb;
    box-shadow: 0 1px 1px 0 black;
}
.btn-seleccionar-curso:hover{
    cursor: pointer;
    background: linear-gradient(90deg, #7390a9 20%, #10308f 100%);
    color: white;
}

.btn-modificar-perfil{
    padding: 2%;
    width: 50%;
    border-radius: 10px;
    border: none;
    margin: 3% 4%;
    font-family: "ITC Benguiat Gothic", sans-serif;
    transition: 800ms;
    background-color: #cbcbcb;
    box-shadow: 0 1px 1px 0 black;
}
.btn-modificar-perfil:hover{
    cursor: pointer;
    background: linear-gradient(90deg, #7390a9 20%, #10308f 100%);
    color: white;
}

.btn-cancelar{
    padding: 6% 10%;
    background-color: #cbcbcb;
    border: none;
    transition: 800ms;
    border-radius: 10px;
    font-family: "ITC Benguiat Gothic", sans-serif;
}
.btn-cancelar:hover{
    background: linear-gradient(90deg, #7390a9 20%, #10308f 100%);
    color: rgba(255, 255, 255, 0.53);
    border: none;
    cursor: pointer;
}